*{
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}
.App{
    min-height: 100vh;
    display: flex;
    background: #AE00FB;
}
.quote{
    margin: auto;
    width: 100%;
    max-width: 400px;
    min-height: 180px;
    padding: 30px;
    background: #DDDDDD;
    border-radius: 4px;
    box-shadow: 4px 4px 1px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}
p{
    color: #000;
    font-size: 17px;
    word-wrap: break-word;
    
}
p:last-of-type{
    margin: 10px 0;
}
button{
    border: none;
    outline: none;
    margin-right: 12px;
}
a{
    text-decoration: none;
    display: inline-block;
    text-align: center;
}
.btn{
    background: #E798AE;
    color: #fff;
    width: 80px;
    padding: 8px 0;
    font-size: 14px;
    border-radius: 4px;
    cursor: pointer;
    transition: all 700ms ease-in;
}
.btn:hover{
   background: #194350; 
}
